  async takePicture(op) {
  const image = await Plugins.Camera.getPhoto({
  correctOrientation: true,
            height: 200,
            width: 200,
                quality: 60,
  allowEditing: true,
            resultType: CameraResultType.DataUrl,
            source: CameraSource.Camera
          });
          this.authenticationService.generateFromImage(image.dataUrl, 200, 200, 0.5, data => {
            switch (op) {
              case 'fotoa': this.fotoimg = data;   this.Form.controls['foto'].setValue( this.authenticationService.procesafoto(this.Form.controls['foto'].value)); break;
                default:
            }
        });
        }
  uploadPWA2(op , controlx) {
         const fileList: FileList = controlx;
         if (fileList && fileList.length > 0) {
                 this.firstFileToBase64(fileList[0]).then((result: string) => {
                 this.authenticationService.generateFromImage(result, 200, 300, 0.5, data => {
                 switch (op) {
                         case 'fotoa':  this.fotoimg = data;    this.Form.controls['foto'].setValue( this.authenticationService.procesafoto(this.Form.controls['foto'].value)); break;
                          default:
                  }
                 });
                 }, (err: any) => {
                 switch (op) {
                 case 'fotoa':  this.fotoimg = null;  this.Form.controls['foto1'].setValue('');  break;
                 default:
                 }
                 });
  }}
  uploadPWA(op) {
  switch (op) {
  case 'fotoa':  if (this.fotoa == null ) { return; }
  this.uploadPWA2(op , this.fotoa.nativeElement.files); break;
  default:
  }
  }
  private firstFileToBase64(fileImage: File): Promise<{}> {
  return new Promise((resolve, reject) => {
  const fileReader: FileReader = new FileReader();
  if (fileReader && fileImage != null) {
  fileReader.readAsDataURL(fileImage);
  fileReader.onload = () => {
  resolve(fileReader.result);
  };
  fileReader.onerror = (error) => {
  reject(error);
  };
  } else {
  reject(new Error('No file found'));
  }
  });
  }
